import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Participantes_reuniao_pic } from '../../../modelo/Participantes_reuniao_pic'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'participantes_reuniao_pic-dados-details',   templateUrl: 'participantes_reuniao_pic_dadosDetails.component.html' }) export class Participantes_reuniao_picDadosDetailsComponent implements OnInit {    participantes_reuniao_pic: Participantes_reuniao_pic;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.participantes_reuniao_pic.Participantes_reuniao_pic";                 let id = +params['id'];         this.participantes_reuniao_pic = new Participantes_reuniao_pic();         this.participantes_reuniao_pic.cd_participantes_reuniao_pic = id;         o.objetoJson = this.participantes_reuniao_pic;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a participantes_reuniao_pic. '; }      this.participantes_reuniao_pic = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


